import Vue from 'vue'
import VueRouter from 'vue-router'
import WebView from '../views/web/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: WebView,
    children: [
      {
        path: '',
        name: 'home',
        component: () => import(/* webpackChunkName: "WebView" */ '../views/web/Home.vue')
      },
      {
        path: 'chrome',
        name: 'chrome',
        component: () => import(/* webpackChunkName: "WebView" */ '../views/web/Chrome.vue')
      },
      {
        path: 'deploy',
        name: 'deploy',
        component: () => import(/* webpackChunkName: "WebView" */ '../views/web/Deploy.vue')
      },
      {
        path: 'proposal',
        name: 'proposal',
        component: () => import(/* webpackChunkName: "WebView" */ '../views/web/Proposal.vue')
      }
    ]
  },
  {
    path: '/admin',
    name: 'admin',
    component: () => import(/* webpackChunkName: "about" */ '../views/admin/index.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
